function prtOnScr(res, opt) {
  //console.log('prtOnScr', res, 'opt', opt, 'prtRes', prtRes);
  if (opt=='+') {
    prtRes += res;
    //console.log('aaa', prtRes);
  } else if (opt=='/') {
    prtRes += `<br /> &nbsp; &nbsp; &nbsp; ${res}`;
    //console.log('bbb', prtRes);
  } else {
    prtRes = `&nbsp; &nbsp; &nbsp; ${res}`;
    //console.log('ccc', prtRes);
  }
  //console.log('prtRes', prtRes);
  document.getElementById("res").innerHTML = prtRes;
} 


function str2rows(s) {   
  let s1 = '', i = 0, j;
  while(i<s.length) {
    let sl = s.slice(i,i+maxRow).padEnd(maxRow);
    //console.log('i', i, sl, sl.length);
    j = sl.indexOf('\n');
    if (j>-1) {
      //console.log('str0')
      sl = sl.slice(0,j);
      i += j+1;
    } else if (sl[maxRow-1]!=' ' && sl[maxRow-1]!='-') {
      //j = sl.lastIndexOf(' ');
      j = Math.max(sl.lastIndexOf(' '), sl.lastIndexOf('-'));
      if (j>-1) {
        //console.log('str1')
        sl = sl.slice(0,j+1);
        i += j+1;
      } else {
        //console.log('str2')
        i += maxRow;
      }
    } else {
      //console.log('str3')
      i += maxRow;
    }
    //s1 += sl+'<br /> &nbsp &nbsp &nbsp';
    s1 += '<br /> &nbsp &nbsp &nbsp '+sl;
  }

  return s1;
}


