const addUsr = async (name) => {
  if (name.length>maxNameLen) {
      console.log('addUsr: name len > max');
      return;
  }
  const name0x = hexName(name);
  console.log('name', name, 'name0x', name0x);
  if(await isUsr(name) == true) {
      console.log('addUsr: name exists');
      return;
  } else if (name.trim().length==0) {
      console.log('addUsr: name empty');
      return;
  }
  const [addr, sAddr, vAddr] = genKeys();
  console.log('addr', addr, addr.length); 
  console.log('sAddr', sAddr, sAddr.length); 
  console.log('vAddr', vAddr, vAddr.length); 
  try {
      const tx = await ndm.addUsr(name0x, '0x'+addr, '0x'+sAddr, '0x'+vAddr);
      await tx.wait();
      console.log('add', tx);
  } catch(e) {
      console.log('add err', e);
  }
} 

