const rmUsr = async (name) => {
  console.log('rmUsr', name);
  const [secretKey, secretSKey, secretVKey] = await readSecKey(name);
  if (secretKey.length==0 || secretSKey.length==0 || secretVKey.length==0) {
    console.log('rmUsr err: no sec keys');
    return;
  }
  console.log('rmUsr', secretKey, secretSKey, secretVKey);
  const secretVKey8 = nacl.util.decodeBase64(secretVKey);
  console.log('rmUsr secretVKey8', secretVKey8);
  const h = kec(str28('rmUsr'+name));   // mod
  const h16 = hexify(h);
  console.log('h16', h16);
  const cmd = signEc(h, secretVKey8);
  console.log('cmd', cmd);
  const cmd1 = ['0x'+h16, '0x'+cmd[2], '0x'+cmd[0], '0x'+cmd[1]];
  console.log('cmd1', cmd1); //return

  const name0x = hexName(name);
  
  try {
      const tx = await ndm.rmUsr(name0x, cmd1);
      await tx.wait();
      //iRm = true;
      console.log('rm', name);
  } catch(e) {
      console.log('rmUsr err', e.reason);
      return;
  }

  secKeys[name0x] = undefined;
  const [fileHandle] = await window.showOpenFilePicker();
  await fileHandle.remove();
}

const genKeys = () => {
  console.log('genKeys');
  const { publicKey, secretKey } = genKeyPair();
  console.log('pubkey', publicKey, publicKey.length); 
  const { publicSKey, secretSKey } = genSigKeyPair();
  console.log('pubSkey', publicSKey, publicSKey.length); 
  const pubKey0x = hexify(publicKey);
  const pubSKey0x = hexify(publicSKey);
  console.log('pubKey0x', pubKey0x, pubSKey0x); 
  console.log('secKey', secretKey, secretKey.length); 
  const secretKey64 = nacl.util.encodeBase64(secretKey);
  const secretSKey64 = nacl.util.encodeBase64(secretSKey);
  console.log('secKey64', secretKey64, secretKey64.length); 
  console.log('secSKey64', secretSKey64, secretSKey64.length); 

  const [vAddr0x, secretHdKey64] = genHdKeys();
  //const vAddr0x = '76d245132937C7bbDB6E5292E324276B8437d4C8';

  saveSecKey(secretKey64, secretSKey64, secretHdKey64);
  return [pubKey0x, pubSKey0x, vAddr0x];   // y!uint8
}

const saveSecKey = async (secretKey64, secretSKey64, secretHdKey64) => {
  const options = {
      types: [
        { description: "Test files", accept: { "text/plain": [".txt"], }, },
      ],
  };
  const handle = await window.showSaveFilePicker(options);
  const writable = await handle.createWritable();
  //await writable.write("Hello World");
  await writable.write(secretKey64);
  await writable.write('\n');
  await writable.write(secretSKey64);
  await writable.write('\n');
  await writable.write(secretHdKey64);
  await writable.close();
}

const readSecKey = async (name) => {
  const name0x = hexName(name); 
  if (secKeys[name0x]==undefined) {
      prtOnScr(`select ${name} secret key file`);
      let fileHandle;
      //document.querySelector(".pick-file").onclick = async () => {
      let file;
      try {
          [fileHandle] = await window.showOpenFilePicker();
          file = await fileHandle.getFile();
      } catch(e) {
        console.log('readSecKey err', e);
        prtOnScr(`no ${name} secret key file`);
        return ['', '', ''];
      }
      let sk = await file.text();
      let i = sk.indexOf('\n');
      const secretKey64 = sk.slice(0,i);
      sk = sk.slice(i+1);
      i = sk.indexOf('\n');
      const secretSKey64 = sk.slice(0,i);
      const secretVKey64 = sk.slice(i+1);
      console.log('secKey', name, secretKey64, secretKey64.length);
      console.log('secSKey', name, secretSKey64, secretSKey64.length);
      console.log('secVKey', name, secretVKey64, secretVKey64.length);
      secKeys[name0x] = [secretKey64, secretSKey64, secretVKey64];
  }
  return secKeys[name0x];
};


