const fwd = async (name) => {
  console.log('fwd', name);
  const [secretKey, secretSKey, secretVKey] = await readSecKey(name);
  console.log('fwd', secretKey, secretSKey, secretVKey);
  const secretVKey8 = nacl.util.decodeBase64(secretVKey);
  console.log('fwd secretVKey8', secretVKey8);
  const h = kec(str28('fwd'+name));   // mod
  const h16 = hexify(h);
  console.log('h16', h16);
  const cmd = signEc(h, secretVKey8);
  console.log('cmd', cmd);
  const cmd1 = ['0x'+h16, '0x'+cmd[2], '0x'+cmd[0], '0x'+cmd[1]];
  console.log('cmd1', cmd1); //return

  try {
      const tx = await ndm.forward(hexName(name), cmd1);
      await tx.wait();
      console.log('fwd', tx);
  } catch(e) {
      console.log('fwd err', e.reason);
  }
}


