const read = async (to) => {   
  const ptrs = await getPtrs(to);
  const l = ptrs.length;
  const txts = [], from = [], txtId = [], tstamp = [];   // frm -> txt
  if (l==0) {
      return [txts, from, txtId, tstamp];
  }

  const [secretKey, secretSkey, secretVKey] = await readSecKey(to);
  console.log('read', to, secretKey, secretSkey, secretVKey);
  const secretKey8 = nacl.util.decodeBase64(secretKey);
  console.log('secretKey8', secretKey8);

  const nonce = [], key = [];
  let i;
  for (i=0; i<l; i++) {
      console.log('read noky i', i, ptrs[i].noky, ptrs[i].noky.length)
      if (ptrs[i].noky.length>0) {
          const [nonce1, key1, txtId1] = decBox(ptrs[i].noky, secretKey8);
          console.log('read txtId1 i', i, txtId1, 'nonce1', nonce1, 'key1', key1);
          nonce.push(nonce1);
          key.push(key1);
          txtId.push('0x'+hexify(txtId1));
          from.push(ptrs[i].from);
          tstamp.push(ptrs[i].tstamp);
      } else {
          console.log('read i emp');
          nonce.push([]);
          key.push([]);
          txtId.push('0x'+Array(64).fill(0).join(''));   // '0x'+'0'.repeat(64)
          from.push(false);
          tstamp.push(0);
      }
  }
  console.log('read txtId', txtId, txtId.length);
  console.log('read from', from, from.length);
  console.log('read tstamp', tstamp, tstamp.length);
  const tsNum = tstamp.map((val) => { return (val==0) ? 0 : Number(val); });

  const side = [], delx = [];
  for (i=0; i<l; i++) {
      console.log('read side i', i, ptrs[i].side, ptrs[i].side.length)
      if (ptrs[i].side.length>0) {
          //const sig1 = decBox1(ptrs[i].side, secretKey8);
          const [sig1, delx1] = decBox2(ptrs[i].side, secretKey8);
          console.log('read sig1 i', i, sig1, 'delx1', delx1);
          side.push(sig1);
          delx.push(delx1);
      } else {
          console.log('read i emp');
          side.push([]);
          delx.push([]);
      }
  }
  console.log('read side', side, side.length);
  console.log('read delx', delx, delx.length);
     
  let encBody;
  try {
      encBody = await ndm.getTxtById(txtId);
      console.log('read encBody', encBody, encBody.length);
  } catch(e) {
      encBody = [];
      console.log('read err', e.reason);
  }
      
  if (encBody.length!=l) {
      console.log('read l', l, 'encBody len', encBody.length);
      return;
  }
  for (i=0; i<l; i++) {
      console.log('read i nonce', i, nonce[i], 'txtId', txtId[i]);
      let body;
      if (encBody[i].length>0 && nonce[i].length>0) {   // Number(txtId[i])==0
          //key[0] += 1;
          try {
              body = decSbox(encBody[i], nonce[i], key[i]);
          } catch(e) {
              console.log('read i decSBox err', e);
              body = '';
          }        
      } else {
          body = '';
          from[i] = false;
          tsNum[i] = 0;
      }
      console.log('read body i', i, body);
      txts.push(body);
  }

  for (i=0; i<l; i++) {
      const j0 = txts[i].indexOf('from:');
      const j1 = txts[i].indexOf(' tstamp:');
      const iFrom = (j0>=0 && j1>j0);
      let ver;
      if (iFrom) {
          const from1 = txts[i].slice(j0+6,j1);
          console.log('read i from1', i, from1);
          const sAddr = await getSAddr(from1);
          const body8 = nacl.util.decodeUTF8(txts[i]);
          const kecBody = kec(body8);
          console.log('sAddr', sAddr);
          const sAddr8 = dehex(sAddr.slice(2)); 
          ver = verify(side[i], kecBody, sAddr8);
          console.log('read ver i', i, ver);
          if (ver!=null) {
              from[i] = from1;
          } else {
              console.log('read i unver');
          }
      } else {
          console.log('read i j0', j0, 'j1', j1);
      }
      if (!iFrom || ver==null) {
          txts[i] = '';
          from[i] = '';
          tsNum[i] = 0;
      }
  }
  
  console.log('read txts', txts, txts.length);
  console.log('read tsNum', tsNum, tsNum.length);
  return [txts, from, txtId, tsNum];
}


const encBox = (noky, addr) => {
  const addr8 = dehex(addr.slice(2)); 
  const encNoky = encrypt(noky, addr8);
  console.log('encBox encNoky', encNoky);
  const encNoky1 = concatUint8([encNoky.cipher, encNoky.nonce, encNoky.ephemKey]);
  console.log('encBox encNoky1', encNoky1);
  const encNoky0x = hexify(encNoky1);
  console.log('encBox encNoky0x', encNoky0x, encNoky0x.length);

  // decnoky=noky
  
  return encNoky0x;
} 


const decBox = (encNoky0x, secretKey) => {   // mod enc ord -> cipher last
  const encNoky1 = dehex(encNoky0x);
  console.log('decBox encNoky1', encNoky1);
  const encNoky = {};
  encNoky.cipher = encNoky1.slice(0,104);
  encNoky.nonce = encNoky1.slice(104, 104+24);
  encNoky.ephemKey = encNoky1.slice(104+24, 104+24+32);
  console.log('decBox encNoky', encNoky);

  const noky = decrypt(encNoky, secretKey); 
  console.log('decBox noky', noky);
  const nonce = noky.slice(0,24);
  const key = noky.slice(24, 24+32);
  const txtId = noky.slice(24+32,noky.length);
  console.log('decBox nonce', nonce);
  console.log('decBox key', key);
  console.log('decBox txtId', txtId, hexify(txtId));
  return [nonce, key, txtId];
}


const decBox2 = (encNoky0x, secretKey) => {
  const encNoky1 = dehex(encNoky0x);
  console.log('decBox2 encNoky1', encNoky1);
  const encNoky = {};
  let l = encNoky1.length-24-32;
  encNoky.cipher = encNoky1.slice(0,l);
  encNoky.nonce = encNoky1.slice(l, l+24);
  encNoky.ephemKey = encNoky1.slice(l+24, l+24+32);
  console.log('decBox2 encNoky', encNoky);

  const noky = decrypt(encNoky, secretKey); 
  console.log('decBox2 noky', noky);
  l = noky.length-32;
  const sig = noky.slice(0,l);
  const delx = noky.slice(l)
  return [sig, delx];
}


const encSbox = (body) => {
  const body8 = nacl.util.decodeUTF8(replChar(body));
  const { nonce, key } = genNonceKeyAes();   // use psw + time in nonce
  console.log('encSbox nonce', nonce);
  console.log('encSbox key', key);
  console.log('encSbox body8', body8);
  const encBody = encryptAes(body8, nonce, key);
  console.log('encSbox encBody', encBody);
  const encBody0x = hexify(encBody);
  console.log('encSbox encBody0x', encBody0x, encBody0x.length);

  //const nonceKey = concatUint8([nonce, key]);
  //console.log('nonceKey', nonceKey, nonceKey.length);
  //return [encBody1, nonceKey];
  return [encBody0x, nonce, key];
}


const decSbox = (encBody0x, nonce, key) => {
  //console.log('decSbox', 'e', encBody0x, 'n', nonce, 'k', key);
  const body8 = decryptAes(dehex(encBody0x), nonce, key);
  console.log('decSbox body8', body8);
  const body = nacl.util.encodeUTF8(body8);
  console.log('decSbox body', body);
  return body;
}


const f = async (from, to, body) => {   // mod .sol, encid put id -> noky
  const t = Number((await ndm.tStamp()));
  body = `from: ${from} tstamp: ${t}   ${body}`;
  console.log('f body', body);

  const [encBody, nonce, key] = encSbox(body);
  console.log('f encBody', encBody, encBody.length);

  const txtId = makeId(from);
  console.log('f txtId', txtId);
  const noky = concatUint8([nonce, key, txtId]);
  console.log('f noky', noky); 

  const [secretKey, secretSKey, secretVKey] = await readSecKey(from);
  console.log('read', from, secretKey, secretSKey, secretVKey);
  //const secretKey8 = nacl.util.decodeBase64(secretKey);
  //console.log('secretKey8', secretKey8);
  const secretSKey8 = nacl.util.decodeBase64(secretSKey);
  console.log('secretSKey8', secretSKey8);

  const body8 = nacl.util.decodeUTF8(replChar(body));
  const kecBody = kec(body8);
  const sig = sign(kecBody, secretSKey8);
  const delxF = nacl.randomBytes(32);
  const delx = nacl.randomBytes(32);
  const sideF = concatUint8([sig, delxF]);
  const side = concatUint8([sig, delx]);

  const toArr = to.split(',');
  const encNoky = []; 
  let i;
  for (i=0; i<toArr.length; i++) {
      console.log('f toArr i', i, toArr[i]);
      const addr = await getAddr(toArr[i]);
      console.log('f addr i', i, addr, addr.length);

      const encNoky1 = encBox(noky, addr);
      console.log('f encNoky1 i', i, encNoky1, encNoky1.length);
      encNoky.push(encNoky1);
      
  }
  console.log('encNoky', encNoky, encNoky.length);  

  const addrF = await getAddr(from);
  console.log('f addrF', addrF, addrF.length);
  const encNokyF = encBox(noky, addrF);
  console.log('f encNokyF', encNokyF, encNokyF.length);  

  const encSide = []; 
  for (i=0; i<toArr.length; i++) {
      console.log('f toArr i', i, toArr[i]);
      const addr = await getAddr(toArr[i]);
      console.log('f addr i', i, addr, addr.length);

      //const encSide1 = encBox(sig, addr);
      const encSide1 = encBox(side, addr);
      console.log('f encSide1 i', i, encSide1, encSide1.length);
      encSide.push(encSide1);
      
  }
  console.log('encSide', encSide, encSide.length);  

  //const encSideF = encBox(sig, addrF);
  const encSideF = encBox(sideF, addrF);
  console.log('f encSideF', encSideF, encSideF.length);  

  const to0x = [];
  for (i=0; i<toArr.length; i++) {
      to0x[i] = hexName(toArr[i]);
  }
  console.log('f to0x', to0x, to0x.length);

  const txtId0x = '0x'+hexify(txtId);
  console.log('f txtId0x', txtId0x);
  const delxF0x = '0x'+hexify(kec(delxF));
  console.log('f delxF0x', delxF0x);

  try {
      const tx = await ndm.f(hexName(from), to0x, encNokyF, encNoky, encSideF, encSide, txtId0x, encBody, delxF0x); 
      await tx.wait();
      console.log('f', tx);
  } catch(e) {
      console.log('f err', e.reason);
  }
} 


