const delF = async (name, num, txtId) => {   // mod num list
  console.log('delF', name, num, txtId);
  const ptrs = await getPtrs(name);
  /* if (ptrs.length<num) {
      return;
  } */

  const [secretKey, secretSKey, secretVKey] = await readSecKey(name);
  console.log('delF', secretKey, secretSKey, secretVKey);
  const secretKey8 = nacl.util.decodeBase64(secretKey);
  console.log('delF secretKey8', secretKey8);

  //const side = [];
  const delx = [];
  let i;
  for (i=0; i<num.length; i++) {   // for j in num
      const j = num[i];
      if (j<ptrs.length) {
          console.log('delF side i', i, j, ptrs[j].side, ptrs[j].side.length)
          if (ptrs[j].side.length>0) {
              //const sig1 = decBox1(ptrs[i].side, secretKey8);
              const [sig1, delx1] = decBox2(ptrs[j].side, secretKey8);
              console.log('delF sig1 j', j, sig1, 'delx1', delx1);
          //side.push(sig1);
          delx.push(delx1);
          } else {
              console.log('delF j emp');
              //side.push([]);
              delx.push('0x'+'0'.repeat(64));
          }
      }
  }
  //console.log('delF side', side, side.length);
  console.log('delF delx', delx, delx.length);

  const secretVKey8 = nacl.util.decodeBase64(secretVKey);
  console.log('delF', 'secretVKey8', secretVKey8);
  const h = kec(str28('delF'+name+num.join('')));   // mod
  const h16 = hexify(h);
  console.log('h16', h16);
  const cmd = signEc(h, secretVKey8);
  console.log('cmd', cmd);
  const cmd1 = ['0x'+h16, '0x'+cmd[2], '0x'+cmd[0], '0x'+cmd[1]];
  console.log('cmd1', cmd1); //return

  const delx0x = [];
  for (i=0; i<delx.length; i++) {   // map
      delx0x[i] = '0x'+hexify(delx[i]);
  }

  try {
      const tx = await ndm.delF(hexName(name), num, txtId, cmd1, delx0x);
      await tx.wait();
      console.log('delF', tx);
  } catch(e) {
      console.log('delF err', e.reason);
  }
}


