import { decodeUTF8 } from "./nacl/nacl-util_mod.js";

const maxLen = 32;


function trimpad(name) {
    return name.trim().padEnd(maxLen,' ');
}

const hexify = (x) => {   // uint8[] -> 0x
    return Array.from(x).map((val) => { return ('0'+val.toString(16)).slice(-2); }).join('');
}

const dehex = (z) => {   // 0x -> uint8[]
    const x = Array.from(z);
    const z8 = new Uint8Array(z.length/2);
    x.forEach((val, ind) => { if (ind%2==0) { z8[ind/2] = parseInt(x[ind]+x[ind+1], 16) } });
    //x.forEach((val, ind) => { if (ind%2==0) { z8[ind/2] = parseInt(x.substr(ind, 2), 16) } });
    return z8;
}

const hexName = (name) => {
    return '0x'+hexify(decodeUTF8(trimpad(name)));   // +g1
}

