function hexifyKey(key) {
    //return Array.from(key).map(val => { return val.toString(16) }).join('');
    return Array.from(key).map(val => { 
        let val0x = val.toString(16);
        if (val0x.length==1) { val0x = '0'+val0x } 
        return val0x;
    }).join(''); 
}

function dehexKey(key0x) {
    //console.log('key0x', key0x);
    const x = Array.from(key0x);
    //console.log('x', x);
    const key8 = new Uint8Array(key0x.length/2);
    //x.forEach((val, ind) => { if (ind%2==0) { key8.push(parseInt(x[ind]+x[ind+1], 16)) } });
    x.forEach((val, ind) => { if (ind%2==0) { key8[ind/2] = parseInt(x[ind]+x[ind+1], 16) } });
    //console.log('key8', key8);
    return key8;
    //return util.encodeUTF8(key8);
}

function replChar(txt) {
    const repl = {};
    repl.frm = [`’`, `“`, `”`];
    repl.srch = [/’/g, /“/g, /”/g];
    repl.to = [`'`, `"`, `"`];

    for (let i=0; i<repl.frm.length; i++) {   // arr methods
        if (txt.indexOf(repl.frm[i])>-1) {
            txt = txt.replace(repl.srch[i], repl.to[i]);
        }
    } 

    return txt;
}

function concatUint8(arr) {
    // arr of uint8arr
    const l = arr.reduce((acc, val) => acc+val.length, 0);
    const b = new Uint8Array(l);
    if (arr.length==0) {
        return b; 
    }

    let m = 0;
    for (let a of arr) {
        b.set(a, m);
        m += a.length;
    }

    return b;
}

function hexify_o(name) {
    return '0x'+Buffer.from(trimpad(name)).toString('hex');
}

function dehex_o(name) {
    return Buffer.from(name.slice(2), 'hex').toString().trim();
}

function hexifyKey_o(key, pre, suf) {
    const br = '\n';
    const l = br.length;
    const lp = pre.length;
    const ls = suf.length;
    const lk = key.length;

    if (key.slice(0,lp)!=pre || key.slice(lk-ls-l, lk-l)!=suf) {
        console.log('err');
        return
    }
    
    let key0 = key.slice(lp+l,lk-ls-l);
    let key64 = '';
    while (key0.length > 0) {
        let i = key0.indexOf(br);
        key64 += key0.slice(0,i);
        key0 = key0.slice(i+l);
    }
    const key0x = Buffer.from(key64, 'base64').toString('hex');
    return key0x;
}

function dehexKey_o(key0x, pre, suf) {
    const br = '\n';
    const key64 = Buffer.from(key0x, 'hex').toString('base64');
    const l = 64;

    let key = '';
    for (let i=0; i<key64.length; i+=l) {
        key += key64.slice(i,i+l)+br;
    }
    key = pre+br+key+suf+br;
    return key;
}

function getPreSuf() {
    const pubPre = '-----BEGIN PUBLIC KEY-----';
    const pubSuf = '-----END PUBLIC KEY-----';
    const prvPre = '-----BEGIN ENCRYPTED PRIVATE KEY-----';
    const prvSuf = '-----END ENCRYPTED PRIVATE KEY-----';
    return [pubPre, pubSuf, prvPre, prvSuf];
}

export { hexify, dehex, hexName, replChar, concatUint8 }