const { keccak256 } = require("ethereum-cryptography/keccak.js");
const { secp256k1 } = require("ethereum-cryptography/secp256k1.js");
const { HDKey } = require("ethereum-cryptography/hdkey.js");
const { hexToBytes, toHex, utf8ToBytes } = require("ethereum-cryptography/utils.js");
const { getRandomBytesSync } = require("ethereum-cryptography/random.js"); 

const nacl = require('tweetnacl'); 
nacl.util = require('tweetnacl-util');

function kec(x) {
    //const x = new Uint8Array([1, 2, 3]);
    console.log('g1 x', x);
    const z = keccak256(x);
    console.log('g1 z', z);
    return z;
}

const hexx = (x) => {
    /* const x = new Uint8Array([1, 2, 3]);
    console.log('g1 x', x);
    const z = toHex(x);
    console.log('z', z);
    const seed = getRandomBytesSync(32);
    console.log('seed', seed); */
    return toHex(x);
}

const str28 = (x) => {
    return utf8ToBytes(x);
}

const genHdKeys = () => {
    console.log('genHDKeys'); 
    const seed = getRandomBytesSync(32);
    console.log('seed', seed);
    const hdkey1 = HDKey.fromMasterSeed(seed);
    //console.log('hdkey1', hdkey1);
    console.log('hdkey1 publicKey', hdkey1.publicKey); // , 'ext', hdkey1.publicExtendedKey);
    console.log('hdkey1 privateKey', hdkey1.privateKey); //, 'bigint', hdkey1.privKey, typeof hdkey1.privKey);
    const publicKey65 = secp256k1.getPublicKey(hdkey1.privateKey, false);
    console.log('publicKey65', publicKey65);
    const vAddr = keccak256(publicKey65.slice(1)).slice(-20);
    console.log('vAddr', vAddr);
    const vAddr0x = toHex(vAddr);
    console.log('vAddr0x', vAddr0x, vAddr0x.length);
    const secretHdKey64 = nacl.util.encodeBase64(hdkey1.privateKey);
    console.log('secHdKey64', secretHdKey64, secretHdKey64.length); 
    return [vAddr0x, secretHdKey64];
} 

const signEc = (hash, privateKey) => {
    console.log('hash', hash, toHex(hash));
    const sig = secp256k1.sign(hash, privateKey);
    console.log('sig', sig);
    const r16 = sig.r.toString(16).padStart(64, '0');
    const s16 = sig.s.toString(16).padStart(64, '0');
    const v16 = (sig.recovery+27).toString(16);
    console.log('r', r16, r16.length);
    console.log('s', s16, s16.length); 
    console.log('v', v16);
    return [r16, s16, v16];
}

